body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.googleIcon{width:8%;margin-left:34%;margin-top:5%}.root{border:"solid $light-gray 1px";width:90%;margin-top:3%}.registerpgfirstText{font-size:24px;margin-top:20px;font-family:"Roboto"," sans-serif"}

*{margin:0;padding:0}.root{border:1px solid lightgrey;box-shadow:1px #807c87;border-radius:10px;margin:2% 8% 10% 10%;padding:4%;width:70%;height:100%}.logo{height:auto;width:30%;margin-left:35%}.firstText{margin-left:35%;font-size:6.1vw;font-family:Roboto}.secondText{margin-left:25%;margin-top:2%;font-size:4.1vw}.dropdownbtn{color:#6495ed;font-weight:bold}.dropbtn{background-color:#f1f1f1;color:#f1f1f1;font-size:16px;border:none;cursor:pointer}.dropbtn:hover,.dropbtn:focus{background-color:#b2c7d8;padding:10px}.dropdown{position:relative;display:inline-block}.dropdown-content{display:none;position:absolute;background-color:#f1f1f1;min-width:220px;overflow:auto;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);z-index:1}.dropdown-content p{color:#000;padding:12px 16px;text-decoration:none;display:block}.dropdown a:hover{background-color:#ddd}.show{display:block}@media only screen and (min-width: 1025px){.root{margin:2% 15% 10% 30%;width:30%}.logo{width:20%}.firstText{font-size:2.1vw}.secondText{margin-left:27%;font-size:1.3vw}}.dropdownbtn{color:#6495ed}

.header{background:#fffff1}.app-bar{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2) !important}.header-title{font-weight:500;color:black}.keep-icon{width:40px;padding-right:8px}.header-title{font-weight:500;padding:0.25em;font-size:21px !important}.header-search{position:relative;border:1px solid #d3d3d3;border-radius:0.5em;background-color:#fffff1;height:3em;opacity:0.30;color:#000;margin-right:0.25em;margin-left:0em;width:55%;margin:0em 4em;display:flex;align-items:center;box-shadow:0 2px 4px -1px rgba(0,0,0,0.2) !important}.header-input-input{padding:0.2em;padding-left:1em;width:100%;height:100%;color:#000 !important;font-size:1em !important}.header-input-input input{font-size:1em;padding:0em 2em;color:#000}.header-icon{padding:10px;height:100%;cursor:pointer;display:flex;color:#000;align-items:center;justify-content:center}.header-icon.search{position:absolute}.header-icon.clear{position:absolute;right:0%}.top-menu{display:flex;padding:0em 5em}.keep-icon{width:40px}.header-logo{display:flex;flex-direction:row;align-items:center;padding:0em 0.25em}.user-profile{display:flex}.user-icon{border-radius:50%;width:40px;height:40px}

